import React from 'react';
import Navigation from "./Navigation"
import { NavLink } from 'react-router-dom';
import Contact from './Contact';
function Apropos() {
    return (
        <div>
            <Navigation />
            <h1 className='titre'>La GUINEE CONAKRY</h1>
            <div className="imag">
                <h2 className='titre2'>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Atque molestiae ex adipisci </h2>
               <NavLink to='/contact'>
                 <div className='plus'>
                  <p className='voir'>VOIR PLUS</p>
                 </div>
               </NavLink>
            </div>
            <Contact/>
        </div>
    );
}

export default Apropos;