import React, { useEffect, useState } from 'react';
import Navigation  from './Navigation';
import axios from 'axios';
function Blog() {
    const [erreur,setErreur] = useState(false)
    const [valeur,setValeur] = useState('')
    const [valeurr,setValeurr] = useState('')
    const [db,setDb] =useState([])
    const recharge =() =>{axios
        .get("http://localhost:3004/article").then((res)=>setDb(res.data))}
    useEffect(()=>{
        recharge()
    },[])
    const defaultt = (e) =>{
       e.preventDefault()
       if(valeur.length<10){
        setErreur(true)
       }
       else{  
        axios.post("http://localhost:3004/article",{
            "nom":valeurr,
             "descrip":valeur,
             "date" :Date.now()    
        });
        setErreur(false)
        setValeur('')
        setValeurr('')
        recharge()
       }
    }
    const datee =(date)=>{
        let newDate =new Date(date).toLocaleDateString("fr-Fr",{
            year: 'numeric',
            month: 'long',
            day : 'numeric',
            hour:'numeric',
            minute:'numeric',
            second:'numeric'
        })
        return newDate
    }
    return (
        <div>
            <Navigation />
           <div className="blog">
               <h1>Mon Blog</h1>
               <form className='formm' onSubmit={(e) => defaultt(e) } >
                 <input
                 value={valeurr}
                  onChange={(e)=>(
                   setValeurr(e.target.value))}
                 type="text" placeholder='Nom'/>
                 <textarea
                 value={valeur}
                 onChange={(e)=>(setValeur(e.target.value))}
                 style={{border:erreur? "2px solid red":''}}
                  name='text' cols="100" rows="20" placeholder='Message'></textarea>
                 {erreur && (<p className='erreur'>Il ya une erreur</p>)}
                 <input type='submit' className='envoyer' value="Envoyer"/>
                </form>
                {
                    db.sort((a,b)=>(b.date - a.date))
                    .map((val)=>(
                        <div key={val.id} className='db'>
                            <p>{val.nom} <em>{ datee(val.date)}</em></p>
                            <p>{val.descrip}</p>
                            <button>Edit</button>
                            <button style={{backgroundColor:'red' ,margin:'7px'}}>Supprimer</button>
                        </div>
                    ))
                }
           </div>
        </div>
    );
}

export default Blog;
