import Navigation from "./Navigation";
import{ useEffect, useState } from 'react';
import axios from 'axios';
import Info from './Info';

function Acceuil(props) {
    const [nombre, setNombre] =useState(250)
    const [donne,setDonne] =useState("")
    const continent =["Africa","America","Asia","Europe","Oceania"]
    const [data ,setData] = useState([]);
    useEffect(()=>{
       axios.get("https://restcountries.com/v3.1/all")
       .then((res)=>
        setData(res.data)
       )},[])
    return (
        <div>
           <Navigation /> 
           <div className="couleur">
             <div className="bare">
                <input type="range" min="1" max="250" className='range' defaultValue={nombre} 
                onChange={(e)=>(setNombre(e.target.value))}/>
                {continent.map((continent)=>(
                    <div key={continent} >
                        <input type="radio" checked={continent===donne} 
                        onChange={(e)=>(setDonne(e.target.id))} id={continent} name='continant'/>
                        <label htmlFor={continent}>{continent}</label>
                    </div>
                ))}
             </div>
             <br/><br/>
             {donne ?(
                <button onClick={()=>(setDonne(''))}>Retournez</button>
             ):''}
             <div className='pays'>
                {data
                .filter((e)=>(e.continents[0].includes(donne)))
                .slice(0,nombre).map((contries,index)=>(
                    <Info key={index} pays={contries}/>
                ))}
        </div>
            </div> 
        </div>
    );
}

export default Acceuil;