import React from 'react';
import { NavLink } from 'react-router-dom';

function Acceuil(props) {
    return (
        <div>
            <div className='navigation'>
          <div className="image">
            <img className='img_react' src="https://uploads.sitepoint.com/wp-content/uploads/2017/04/1493235373large_react_apps_A-01.png" alt="imaage" />
            <p className='nom_p'>React js</p>
          </div> 
          <ul className='ul_nav'>
            <li className={(nav)=>(nav.isActive ? "active" : "acti")}><NavLink to="/" className={'navlink'}>Acceuil</NavLink></li>
            <li className={(nav)=>(nav.isActive ? "active" : "")}><NavLink to="/apropos" className='navlink'>Apropos</NavLink></li>
            <li className={(nav)=>(nav.isActive ? "active" : "")}><NavLink to="/blog" className='navlink'>Blog</NavLink></li>
          </ul>  
        </div>
        <br /><br /><br />
        </div>
    );
}

export default Acceuil;