import React from 'react';

function Info(props) {
    return (
        <div className='info_pays'>
            <img className='img_pays' src={props.pays.flags.svg} alt={"nom " + props.pays.translations.fra.common} />
            <p >{props.pays.translations.fra.common}</p>
            <p>{props.pays.capital}</p>
            <p>{props.pays.population.toLocaleString()}</p>
        </div>
    );
}

export default Info;